/* tslint:disable:no-unused-variable */
let primitives = [
                    "string",
                    "boolean",
                    "double",
                    "integer",
                    "long",
                    "float",
                    "number",
                    "any"
                 ];

let enumsMap: {[index: string]: any} = {
        "AuthenticatedState": AuthenticatedState,
        "ChannelType": ChannelType,
        "DeviceType": DeviceType,
        "LoggingType": LoggingType,
        "MetricType": MetricType,
        "MobilePlatformType": MobilePlatformType,
        "OptionType": OptionType,
        "ScreenOrientationType": ScreenOrientationType,
}

let typeMap: {[index: string]: any} = {
    "Action": Action,
    "Address": Address,
    "AnalyticsPayload": AnalyticsPayload,
    "AnalyticsRequest": AnalyticsRequest,
    "AnalyticsResponse": AnalyticsResponse,
    "Application": Application,
    "AudienceManager": AudienceManager,
    "Browser": Browser,
    "Context": Context,
    "CustomerId": CustomerId,
    "DeliveryRequest": DeliveryRequest,
    "DeliveryResponse": DeliveryResponse,
    "ExecuteRequest": ExecuteRequest,
    "ExecuteResponse": ExecuteResponse,
    "ExperienceCloud": ExperienceCloud,
    "Geo": Geo,
    "MboxRequest": MboxRequest,
    "MboxRequestAllOf": MboxRequestAllOf,
    "MboxResponse": MboxResponse,
    "Metric": Metric,
    "MobilePlatform": MobilePlatform,
    "Notification": Notification,
    "NotificationAllOf": NotificationAllOf,
    "NotificationMbox": NotificationMbox,
    "NotificationPageLoad": NotificationPageLoad,
    "NotificationView": NotificationView,
    "Option": Option,
    "Order": Order,
    "PageLoadResponse": PageLoadResponse,
    "PrefetchMboxResponse": PrefetchMboxResponse,
    "PrefetchMboxResponseAllOf": PrefetchMboxResponseAllOf,
    "PrefetchRequest": PrefetchRequest,
    "PrefetchResponse": PrefetchResponse,
    "Product": Product,
    "Property": Property,
    "QAMode": QAMode,
    "QAModePreviewIndex": QAModePreviewIndex,
    "RequestDetails": RequestDetails,
    "Screen": Screen,
    "Trace": Trace,
    "UnexpectedError": UnexpectedError,
    "View": View,
    "ViewRequest": ViewRequest,
    "ViewRequestAllOf": ViewRequestAllOf,
    "VisitorId": VisitorId,
    "Window": Window,
}

export class ObjectSerializer {
    public static findCorrectType(data: any, expectedType: string) {
        if (data == undefined) {
            return expectedType;
        } else if (primitives.indexOf(expectedType.toLowerCase()) !== -1) {
            return expectedType;
        } else if (expectedType === "Date") {
            return expectedType;
        } else {
            if (enumsMap[expectedType]) {
                return expectedType;
            }

            if (!typeMap[expectedType]) {
                return expectedType; // w/e we don't know the type
            }

            // Check the discriminator
            let discriminatorProperty = typeMap[expectedType].discriminator;
            if (discriminatorProperty == null) {
                return expectedType; // the type does not have a discriminator. use it.
            } else {
                if (data[discriminatorProperty]) {
                    var discriminatorType = data[discriminatorProperty];
                    if(typeMap[discriminatorType]){
                        return discriminatorType; // use the type given in the discriminator
                    } else {
                        return expectedType; // discriminator did not map to a type
                    }
                } else {
                    return expectedType; // discriminator was not present (or an empty string)
                }
            }
        }
    }

    public static serialize(data: any, type: string) {
        if (data == undefined) {
            return data;
        } else if (primitives.indexOf(type.toLowerCase()) !== -1) {
            return data;
        } else if (type.lastIndexOf("Array<", 0) === 0) { // string.startsWith pre es6
            let subType: string = type.replace("Array<", ""); // Array<Type> => Type>
            subType = subType.substring(0, subType.length - 1); // Type> => Type
            let transformedData: any[] = [];
            for (let index in data) {
                let date = data[index];
                transformedData.push(ObjectSerializer.serialize(date, subType));
            }
            return transformedData;
        } else if (type === "Date") {
            return data.toISOString();
        } else {
            if (enumsMap[type]) {
                return data;
            }
            if (!typeMap[type]) { // in case we dont know the type
                return data;
            }

            // Get the actual type of this object
            type = this.findCorrectType(data, type);

            // get the map for the correct type.
            let attributeTypes = typeMap[type].getAttributeTypeMap();
            let instance: {[index: string]: any} = {};
            for (let index in attributeTypes) {
                let attributeType = attributeTypes[index];
                instance[attributeType.baseName] = ObjectSerializer.serialize(data[attributeType.name], attributeType.type);
            }
            return instance;
        }
    }

    public static deserialize(data: any, type: string) {
        // polymorphism may change the actual type.
        type = ObjectSerializer.findCorrectType(data, type);
        if (data == undefined) {
            return data;
        } else if (primitives.indexOf(type.toLowerCase()) !== -1) {
            return data;
        } else if (type.lastIndexOf("Array<", 0) === 0) { // string.startsWith pre es6
            let subType: string = type.replace("Array<", ""); // Array<Type> => Type>
            subType = subType.substring(0, subType.length - 1); // Type> => Type
            let transformedData: any[] = [];
            for (let index in data) {
                let date = data[index];
                transformedData.push(ObjectSerializer.deserialize(date, subType));
            }
            return transformedData;
        } else if (type === "Date") {
            return new Date(data);
        } else {
            if (enumsMap[type]) {// is Enum
                return data;
            }

            if (!typeMap[type]) { // dont know the type
                return data;
            }
            let instance = new typeMap[type]();
            let attributeTypes = typeMap[type].getAttributeTypeMap();
            for (let index in attributeTypes) {
                let attributeType = attributeTypes[index];
                instance[attributeType.name] = ObjectSerializer.deserialize(data[attributeType.baseName], attributeType.type);
            }
            return instance;
        }
    }
}
